.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo{
    width: 100px;
}

.nav-items{
    padding: 0px 20px;
}
.nav-items > ul{
    font-size: 24px;
    display: flex;
    list-style-type: none;
}

.nav-items > ul > li {
    margin: 10px;
    padding: 10px;
}

.res-container {
    display: flex;
    flex-wrap: wrap;
}

.res-card{
    margin: 5px;
    width: 200px;
    padding: 5px;
}

.res-card:hover {
    cursor: pointer;
    border: 1px solid black;
}
.search{
    padding: 10px;
}

.res-logo{
    width: 100%;
}